<template>
  <div class="login-container" :style="{ backgroundColor: colorBg }" v-if="loadData">
    <el-form
      ref="loginForm"
      :model="loginForm"
      class="login-form"
      auto-complete="on"
      label-position="left"
    >
      <img
        class="logo-wdc"
        width="150px"
        :src="srcLogo"
        alt="Logo"
      />
      <div class="title-container">
        <h3 class="title">Accesos Manager</h3>
      </div>

      <el-form-item prop="username">
        <span class="svg-container">
          <fa :icon="['fas', 'user']" />
        </span>
        <el-input
          v-model="loginForm.email"
          frontRules="required|email"
          labelValidation="Email"
          type="email"
          placeholder="Email"
          tabindex="1"
          inputRef="email"
        />
      </el-form-item>

      <el-form-item prop="password">
        <span class="svg-container">
          <fa :icon="['fas', 'lock']" />
        </span>
        <el-input
          :key="passwordType"
          ref="password"
          v-model="loginForm.password"
          :type="passwordType"
          placeholder="Password"
          name="password"
          tabindex="2"
          auto-complete="on"
          @keyup.enter.native="handleLogin"
        />
        <span class="pass" @click="showPwd">
          <fa
            :icon="
              passwordType === 'password'
                ? ['fas', 'eye']
                : ['fas', 'eye-slash']
            "
          />
        </span>
      </el-form-item>

      <el-button
        :loading="loading"
        type="primary"
        style="width: 100%; margin-bottom: 30px"
        @click.native.prevent="handleLogin"
        >Login</el-button
      >
    </el-form>
  </div>
</template>

<script>
import { Message } from 'element-ui'

import { useStoreUser } from '../../stores/userStore'
import { useStoreSettings } from '../../stores/settingsStore'
import { useStoreConfig } from '../../stores/configStore'

const settingsStore = useStoreSettings()
const userStore = useStoreUser()
const configStore = useStoreConfig()
export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        email: '',
        password: ''
      },
      configStore,
      loading: false,
      passwordType: 'password',
      srcLogo: '@/assets/images/logo.png',
      colorBg: '#283443',
      loadData: false
    }
  },
  watch: {
    $route: {
      handler: function (route) {
        this.redirect = route.query && route.query.redirect
      },
      immediate: true
    }
  },
  async created () {
    await settingsStore.fetchLogin()
      .then(response => {
        if (settingsStore.login.logo != null || settingsStore.login.logo !== '') {
          this.srcLogo = configStore.baseUrl + settingsStore.login.logo
          this.colorBg = settingsStore.login.color
        }
      })
      .finally(() => {
        this.loadData = true
        // console.log(this.loadData)
      })
  },
  methods: {
    showPwd () {
      if (this.passwordType === 'password') {
        this.passwordType = ''
      } else {
        this.passwordType = 'password'
      }
      this.$nextTick(() => {
        this.$refs.password.focus()
      })
    },
    handleLogin () {
      this.loading = true

      // REMOVE STORE
      this.$store
        .dispatch('user/login', this.loginForm)
        .then(() => {
          this.$message({
            showClose: true,
            message: 'Acceso Concedido!',
            type: 'success'
          })
          this.$router.push({ path: '/dashboard' })
          // this.$router.push({ name: 'admin' })
          settingsStore.fetchSettings().then((response) => {})
        })
        .catch((error) => {
          Message.error(error.response.data.messages[0] || 'Error')
        })
        .finally(() => {
          this.loading = false
        })
      // REMPLACE STORE
      userStore
        .login(this.loginForm)
        .then(() => {
          // this.$message({
          //   showClose: true,
          //   message: 'Acceso Concedido!',
          //   type: 'success'
          // })
          // this.$router.push({ path: '/dashboard' })
          // this.$router.push({ name: 'admin' })
        })
        .catch((error) => {
          Message.error(error.response.data.messages[0] || 'Error')
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>

<style lang="scss">
/* 修复input 背景不协调 和光标变色 */
/* Detail see https://github.com/PanJiaChen/vue-element-admin/pull/927 */

$bg: #283443;
$light_gray: #fff;
$cursor: #fff;

@supports (-webkit-mask: none) and (not (cater-color: $cursor)) {
  .login-container .el-input input {
    color: $cursor;
  }
}

/* reset element-ui css */
.login-container {
  .el-input {
    display: inline-block;
    height: 47px;
    width: 85%;

    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
      caret-color: $cursor;

      &:-webkit-autofill {
        box-shadow: 0 0 0px 1000px $bg inset !important;
        -webkit-text-fill-color: $cursor !important;
      }
    }
  }

  .el-form-item {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
  }
}
</style>

<style lang="scss" scoped>
// $bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;

.login-container {
  min-height: 100%;
  width: 100%;
  //background-color: $bg;
  overflow: hidden;

  .login-form {
    position: relative;
    width: 520px;
    max-width: 100%;
    padding: 120px 35px 0;
    margin: 0 auto;
    overflow: hidden;
  }

  .tips {
    font-size: 14px;
    color: #fff;
    margin-bottom: 10px;

    span {
      &:first-of-type {
        margin-right: 16px;
      }
    }
  }

  .svg-container {
    padding: 6px 5px 6px 15px;
    color: $dark_gray;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
  }
  .pass {
    padding: 6px 5px 6px 10px;
    color: $dark_gray;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    user-select: none;
  }

  .title-container {
    position: relative;

    .title {
      font-size: 26px;
      color: $light_gray;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
  }

  .show-pwd {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 16px;
    color: $dark_gray;
    cursor: pointer;
    user-select: none;
  }
  .logo-wdc {
    margin: auto;
    display: flex;
    margin-bottom: 20px;
  }
}
</style>
